import { useEffect, useState, useContext } from 'react';
import { 
  Box, 
  Typography, 
  Card, 
  CardContent, 
  Grid, 
  Divider,
  Tab,
  Tabs,
  IconButton,
  useTheme
} from '@mui/material';
import { 
  WbSunny, 
  Cloud, 
  Opacity, 
  AcUnit, 
  Thunderstorm, 
  Grain, 
  Air,
  Refresh,
  LocationOn,
  Thermostat,
  WbSunnyOutlined
} from '@mui/icons-material';
import { SettingsContext } from '../../context/SettingsContext';

const WeatherWidget = (props) => {
  const { sx, ...otherProps } = props;
  const { language, themeMode } = useContext(SettingsContext);
  const [weatherData, setWeatherData] = useState(null);
  const [hourlyForecast, setHourlyForecast] = useState(null);
  const [locationName, setLocationName] = useState(null);
  const [aqiData, setAqiData] = useState(null);
  const [error, setError] = useState(null);
  const [locationError, setLocationError] = useState(null);
  const [tabValue, setTabValue] = useState(0);
  const theme = useTheme();
  
  // Check if dark mode is enabled to determine colors
  const isDarkMode = themeMode === "dark";

  // Map WMO Weather Code to weather description and icon
  const weatherCodeMap = {
    0: { 
      description: "Clear sky", 
      icon: <WbSunny sx={{ color: isDarkMode ? "grey.400" : "orange" }} /> 
    },
    1: { 
      description: "Mainly clear", 
      icon: <WbSunny sx={{ color: isDarkMode ? "grey.400" : "orange" }} /> 
    },
    2: { 
      description: "Partly cloudy", 
      icon: <Cloud sx={{ color: isDarkMode ? "grey.400" : "gray" }} /> 
    },
    3: { 
      description: "Overcast", 
      icon: <Cloud sx={{ color: isDarkMode ? "grey.400" : "darkgray" }} /> 
    },
    45: { 
      description: "Fog", 
      icon: <Grain sx={{ color: isDarkMode ? "grey.400" : "lightgray" }} /> 
    },
    48: { 
      description: "Depositing rime fog", 
      icon: <Grain sx={{ color: isDarkMode ? "grey.400" : "lightgray" }} /> 
    },
    51: { 
      description: "Light drizzle", 
      icon: <Opacity sx={{ color: isDarkMode ? "grey.400" : "lightblue" }} /> 
    },
    53: { 
      description: "Moderate drizzle", 
      icon: <Opacity sx={{ color: isDarkMode ? "grey.400" : "lightblue" }} /> 
    },
    55: { 
      description: "Dense drizzle", 
      icon: <Opacity sx={{ color: isDarkMode ? "grey.400" : "blue" }} /> 
    },
    61: { 
      description: "Slight rain", 
      icon: <Opacity sx={{ color: isDarkMode ? "grey.400" : "lightblue" }} /> 
    },
    63: { 
      description: "Moderate rain", 
      icon: <Opacity sx={{ color: isDarkMode ? "grey.400" : "blue" }} /> 
    },
    65: { 
      description: "Heavy rain", 
      icon: <Opacity sx={{ color: isDarkMode ? "grey.400" : "darkblue" }} /> 
    },
    71: { 
      description: "Slight snow", 
      icon: <AcUnit sx={{ color: isDarkMode ? "grey.400" : "lightblue" }} /> 
    },
    73: { 
      description: "Moderate snow", 
      icon: <AcUnit sx={{ color: isDarkMode ? "grey.400" : "blue" }} /> 
    },
    75: { 
      description: "Heavy snow", 
      icon: <AcUnit sx={{ color: isDarkMode ? "grey.400" : "darkblue" }} /> 
    },
    80: { 
      description: "Slight rain showers", 
      icon: <Opacity sx={{ color: isDarkMode ? "grey.400" : "lightblue" }} /> 
    },
    81: { 
      description: "Moderate rain showers", 
      icon: <Opacity sx={{ color: isDarkMode ? "grey.400" : "blue" }} /> 
    },
    82: { 
      description: "Violent rain showers", 
      icon: <Opacity sx={{ color: isDarkMode ? "grey.400" : "darkblue" }} /> 
    },
    95: { 
      description: "Thunderstorm", 
      icon: <Thunderstorm sx={{ color: isDarkMode ? "grey.400" : "purple" }} /> 
    },
    96: { 
      description: "Thunderstorm with slight hail", 
      icon: <Thunderstorm sx={{ color: isDarkMode ? "grey.400" : "purple" }} /> 
    },
    99: { 
      description: "Thunderstorm with heavy hail", 
      icon: <Thunderstorm sx={{ color: isDarkMode ? "grey.400" : "darkpurple" }} /> 
    },
  };

  // AQI categories and colors
  const aqiCategoryMap = {
    1: { label: "Good", color: isDarkMode ? "grey.400" : "green" },
    2: { label: "Fair", color: isDarkMode ? "grey.400" : "#a3c853" },
    3: { label: "Moderate", color: isDarkMode ? "grey.400" : "yellow" },
    4: { label: "Poor", color: isDarkMode ? "grey.400" : "orange" },
    5: { label: "Very Poor", color: isDarkMode ? "grey.400" : "red" }
  };

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  const handleRefresh = () => {
    getLocation();
  };

  const fetchWeather = async (latitude, longitude) => {
    setError(null);
    try {
      // Get all required data in a single API request
      const weatherResponse = await fetch(
        `https://api.open-meteo.com/v1/forecast?latitude=${latitude}&longitude=${longitude}&current=temperature_2m,apparent_temperature,relative_humidity_2m,precipitation,weather_code,windspeed_10m,winddirection_10m&hourly=temperature_2m,apparent_temperature,weathercode,relativehumidity_2m,precipitation_probability,precipitation,windspeed_10m,winddirection_10m,uv_index&daily=temperature_2m_max,temperature_2m_min,sunset&timeformat=unixtime`
      );
      
      if (!weatherResponse.ok) {
        throw new Error("Failed to fetch weather data");
      }
      const data = await weatherResponse.json();
      
      // Set current weather data
      const currentWeatherData = {
        temperature: data.current.temperature_2m,
        apparent_temperature: data.current.apparent_temperature,
        humidity: data.current.relative_humidity_2m,
        precipitation: data.current.precipitation,
        weathercode: data.current.weather_code,
        time: data.current.time,
        windspeed: data.current.windspeed_10m,
        winddirection: data.current.winddirection_10m,
        sunset: data.daily?.sunset[0],
        temperatureMax: data.daily?.temperature_2m_max[0],
        temperatureMin: data.daily?.temperature_2m_min[0]
      };
      
      setWeatherData(currentWeatherData);
      
      // Process hourly forecast data (next 24 hours)
      const currentTime = new Date().getTime() / 1000; // Current Unix timestamp
      const next24Hours = data.hourly.time
        .map((time, index) => ({
          time,
          temperature: data.hourly.temperature_2m[index],
          apparent_temperature: data.hourly.apparent_temperature[index],
          weathercode: data.hourly.weathercode[index],
          humidity: data.hourly.relativehumidity_2m[index],
          precipitation_probability: data.hourly.precipitation_probability[index],
          precipitation: data.hourly.precipitation[index],
          windspeed: data.hourly.windspeed_10m[index],
          winddirection: data.hourly.winddirection_10m[index],
          uv_index: data.hourly.uv_index ? data.hourly.uv_index[index] : undefined
        }))
        .filter(item => item.time >= currentTime)
        .slice(0, 24); // Get the next 24 hours of data
        
      setHourlyForecast(next24Hours);
      
      // Fetch AQI data
      try {
        const aqiResponse = await fetch(
          `https://air-quality-api.open-meteo.com/v1/air-quality?latitude=${latitude}&longitude=${longitude}&current=european_aqi`
        );
        
        if (aqiResponse.ok) {
          const aqiData = await aqiResponse.json();
          setAqiData({
            europeanAqi: aqiData.current?.european_aqi
          });
        }
      } catch (aqiError) {
        console.error("Failed to fetch AQI data:", aqiError);
        // Don't set the main error - AQI is non-critical
      }
  
      // Get location data
      const locationResponse = await fetch(
        `https://nominatim.openstreetmap.org/reverse?format=json&lat=${latitude}&lon=${longitude}`
      );
      if (locationResponse.ok) {
        const locationData = await locationResponse.json();
        const address = locationData.address;
        
        const suburb = address.suburb;
        const city = address.city || address.town || address.village;
        
        let locationName = "Unknown location";
        if (suburb && city) {
          locationName = `${suburb}, ${city}`;
        } else if (suburb) {
          locationName = suburb;
        } else if (city) {
          locationName = city;
        }
      
        setLocationName(locationName);
      }

    } catch (err) {
      setError(err.message);
    }
  };

  const getLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          fetchWeather(latitude, longitude);
        },
        (err) => {
          setLocationError("Unable to retrieve location. Please allow location access.");
        }
      );
    } else {
      setLocationError("Geolocation is not supported by this browser.");
    }
  };

  useEffect(() => {
    getLocation();
  }, []);

  if (locationError) {
    return (
      <Card sx={{ margin: 'auto', boxShadow: 3 }}>
        <CardContent sx={{ textAlign: 'center', color: 'error.main' }}>
          <Typography variant="body1">{locationError}</Typography>
        </CardContent>
      </Card>
    );
  }

  if (error) {
    return (
      <Card sx={{ margin: 'auto', boxShadow: 3 }}>
        <CardContent sx={{ textAlign: 'center', color: 'error.main' }}>
          <Typography variant="body1">Error: {error}</Typography>
        </CardContent>
      </Card>
    );
  }

  const weatherInfo = weatherData ? weatherCodeMap[weatherData.weathercode] || { description: "Unknown weather", icon: <Cloud sx={{ color: isDarkMode ? "grey.400" : "inherit" }} /> } : null;

  // Format time
  const formatTime = (unixTime) => {
    if (!unixTime) return '';
    const date = new Date(unixTime * 1000);
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  };

  // Format date
  const formatDate = (unixTime) => {
    if (!unixTime) return '';
    const date = new Date(unixTime * 1000);
    return date.toLocaleDateString([], { month: 'short', day: 'numeric' });
  };

  // Calculate max rain chance in the next 12 hours
  const calculateMaxRainChance = () => {
    if (!hourlyForecast || hourlyForecast.length === 0) return 'N/A';
    
    const probabilities = hourlyForecast
      .slice(0, 12) // Only take the first 12 hours
      .filter(hour => hour.precipitation_probability !== undefined)
      .map(hour => hour.precipitation_probability);
    
    if (probabilities.length === 0) return 'N/A';
    
    const maxProbability = Math.max(...probabilities);
    return `${maxProbability}%`;
  };

  // Get AQI category
  const getAqiCategory = (aqiValue) => {
    if (!aqiValue) return null;
    
    if (aqiValue <= 20) return aqiCategoryMap[1];
    if (aqiValue <= 40) return aqiCategoryMap[2];
    if (aqiValue <= 60) return aqiCategoryMap[3];
    if (aqiValue <= 80) return aqiCategoryMap[4];
    return aqiCategoryMap[5];
  };

  const aqiCategory = aqiData?.europeanAqi ? getAqiCategory(aqiData.europeanAqi) : null;

  return (
    <Card sx={{ 
      ...sx, 
      margin: 'auto', 
      borderRadius: 2,
      boxShadow: 3,
      overflow: 'hidden'
    }}>
      {/* Header bar */}
      <Box sx={{ 
        p: 1,
        display: 'flex', 
        justifyContent: 'space-between', 
        alignItems: 'center', 
        bgcolor: isDarkMode ? 'grey.800' : theme.palette.primary.main,
        color: 'white'
      }}>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <LocationOn />
          <Box sx={{ ml: 1 }}>
            <Typography variant="Body2">
              {locationName || "--"}
            </Typography>
          </Box>
        </Box>
        <IconButton color="inherit" onClick={handleRefresh}>
          <Refresh />
        </IconButton>
      </Box>

      {/* Tabs */}
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs 
          value={tabValue} 
          onChange={handleTabChange} 
          variant="fullWidth"
          indicatorColor="primary"
          textColor="primary"
        >
          <Tab label="Current" />
          <Tab label="24-Hour" />
        </Tabs>
      </Box>

      {/* Current weather tab content */}
      {tabValue === 0 && (
        <CardContent sx={{  display: 'flex', alignItems: 'center',  textAlign: 'center' }}>
          <Grid container sx={{ height: '100%' }}>
            {/* Left column: Weather info */}
            <Grid item xs={8} sx={{ display: 'flex', alignItems: 'center' }}>
              <Box sx={{ 
                display: 'flex', 
                flexDirection: 'column', 
                alignItems: 'center',
                justifyContent: 'center',
                width: '100%',
                height: '100%'
              }}>
                {weatherData && (
                  <>
                    <Box sx={{ transform: 'scale(1.8)', mb: 2 }}>
                      {weatherInfo?.icon || <Cloud sx={{ color: isDarkMode ? "grey.400" : "inherit" }} />}
                    </Box>
                    <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                      {weatherData ? Math.round(weatherData.temperature) : '--'}°C
                    </Typography>
                    
                    {/* "Feels" temperature below real temperature */}
                    <Typography variant="body2" sx={{ color: 'text.secondary', mt: 0.5 }}>
                      Feels {weatherData?.apparent_temperature !== undefined ? `${Math.round(weatherData.apparent_temperature)}°C` : '--'}
                    </Typography>
                    
                    <Typography variant="subtitle1" sx={{ color: 'text.secondary', mt: 1 }}>
                      {weatherInfo?.description || ''}
                    </Typography>
                    
                    {/* High/Low temperature */}
                    <Box sx={{ display: 'flex', mt: 1, justifyContent: 'center' }}>
                      <Typography variant="subtitle2" sx={{ fontWeight: 'bold' }}>
                        H: {weatherData.temperatureMax ? Math.round(weatherData.temperatureMax) : '--'}°C
                        {' '}
                        L: {weatherData.temperatureMin ? Math.round(weatherData.temperatureMin) : '--'}°C
                      </Typography>
                    </Box>
                  </>
                )}
              </Box>
            </Grid>
            
            {/* Right column: Rain, AQI, UV (in that order) */}
            <Grid item xs={4} sx={{ 
              display: 'flex', 
              flexDirection: 'column', 
              justifyContent: 'space-around',
              height: '100%'
            }}>
              <Box sx={{ 
                height: '100%', 
                display: 'flex', 
                flexDirection: 'column', 
                justifyContent: 'space-evenly'
              }}>
                {/* Rain Chance */}
                <Box sx={{ p: 1, display: 'flex', alignItems: 'center' }}>
                  <Opacity sx={{ color: isDarkMode ? "grey.400" : theme.palette.info.dark, mr: 1 }} />
                  <Box>
                    <Typography variant="body2">Rain</Typography>
                    <Typography variant="body1" sx={{ fontWeight: 'bold' }}>
                      {hourlyForecast ? calculateMaxRainChance() : 'N/A'}
                    </Typography>
                  </Box>
                </Box>
                
                {/* AQI (Air Quality Index) */}
                <Box sx={{ p: 1, display: 'flex', alignItems: 'center' }}>
                  <Air sx={{ color: isDarkMode ? "grey.400" : (aqiCategory?.color || "grey"), mr: 1 }} />
                  <Box>
                    <Typography variant="body2">AQI</Typography>
                    <Typography variant="body1" sx={{ fontWeight: 'bold' }}>
                      {aqiData?.europeanAqi ? (
                        <>
                          {aqiData.europeanAqi}
                        </>
                      ) : (
                        'N/A'
                      )}
                    </Typography>
                  </Box>
                </Box>
                
                {/* UV Index */}
                <Box sx={{ p: 1, display: 'flex', alignItems: 'center' }}>
                  <WbSunnyOutlined sx={{ color: isDarkMode ? "grey.400" : "orange", mr: 1 }} />
                  <Box>
                    <Typography variant="body2">UV</Typography>
                    {hourlyForecast && hourlyForecast.length > 0 ? (
                      <Typography 
                        variant="body1" 
                        sx={{ fontWeight: 'bold' }}
                      >
                        {hourlyForecast[0].uv_index !== undefined ? 
                          hourlyForecast[0].uv_index.toFixed(1) : 
                          'N/A'}
                      </Typography>
                    ) : (
                      <Typography variant="body1" sx={{ fontWeight: 'bold' }}>
                        N/A
                      </Typography>
                    )}
                  </Box>
                </Box>
              </Box>
            </Grid>
          </Grid>
        </CardContent>
      )}

      {/* 24-hour forecast tab content */}
      {tabValue === 1 && (
        <CardContent>
          <Box sx={{ overflowX: 'auto' }}>
            <Box sx={{ display: 'flex', pb: 1, minWidth: 'min-content' }}>
              {hourlyForecast && hourlyForecast.map((hour, index) => {
                const hourWeatherInfo = weatherCodeMap[hour.weathercode] || { 
                  description: "Unknown", 
                  icon: <Cloud sx={{ color: isDarkMode ? "grey.400" : "inherit" }} /> 
                };
                return (
                  <Box 
                    key={index} 
                    sx={{ 
                      mr: 1,
                      display: 'flex', 
                      flexDirection: 'column', 
                      alignItems: 'center',
                      p: 1,
                      borderRadius: 1,
                      bgcolor: index === 0 ? 'rgba(0, 0, 0, 0.04)' : 'transparent'
                    }}
                  >
                    <Typography variant="caption" sx={{ fontWeight: index === 0 ? 'bold' : 'normal' }}>
                      {formatTime(hour.time)}
                    </Typography>
                    <Box sx={{ my: 1 }}>
                      {hourWeatherInfo.icon}
                    </Box>
                    <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                      {Math.round(hour.temperature)}°C
                    </Typography>
                    <Typography variant="caption" color="text.secondary">
                      {Math.round(hour.apparent_temperature)}°C
                    </Typography>
                    <Box sx={{ display: 'flex', alignItems: 'center', mt: 1 }}>
                      <Opacity sx={{ fontSize: 12, color: isDarkMode ? "grey.400" : theme.palette.info.light }} />
                      <Typography variant="caption" sx={{ ml: 0.5 }}>
                        {hour.precipitation_probability !== undefined ? `${hour.precipitation_probability}%` : 'N/A'}
                      </Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center', mt: 0.5 }}>
                      <WbSunnyOutlined sx={{ fontSize: 12, color: isDarkMode ? "grey.400" : "grey" }} />
                      <Typography variant="caption" sx={{ ml: 0.5 }}>
                        {hour.uv_index !== undefined ? hour.uv_index.toFixed(1) : '-'}
                      </Typography>
                    </Box>
                  </Box>
                );
              })}
            </Box>
          </Box>
        </CardContent>
      )}
    </Card>
  );
};

export default WeatherWidget;