import { useContext } from 'react';
import { Box } from '@mui/material';
import CustomAppBar from '../../components/shared-components/AppBar/AppBar';
import { SettingsContext } from '../../context/SettingsContext';
import Footer from '../../components/shared-components/Footer';
import WeatherWidget from '../../components/dashboard-components/WeatherWidget';

const Dashboard = () => {
  const { language, themeMode} = useContext(SettingsContext);

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
      }}
    >
      {/* 上部 100vh 的部分 */}
      <Box
        sx={{
          minHeight: '100vh',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <CustomAppBar />
        
        <Box sx={{ mt: 8 }}>
          <WeatherWidget sx={{ width: "400px", height: "400px" }}/>
        </Box>
      </Box>

      {/* 下部 Footer */}
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          backgroundColor: 'background.default',
          borderTop: (theme) => ({
            xs: 'none',
            md: `1px solid ${theme.palette.divider}`,
          }),
          maxWidth: (theme) => theme.breakpoints.values.lg, 
          width: '100%', 
          mx: 'auto',
        }}
      >
        <Footer />
      </Box>

      
    </Box>
  );
};

export default Dashboard;
